import logo from "./logo.svg";
import React, { useState } from "react";
import "./index.css";
import Header from "./tabs/Header";
import Main from "./tabs/Main";
import ContactOverlay from "./tabs/ContactOverlay";
import axios from "axios";

const backend = "https://34.138.41.238:3000";
function App() {
  const [contactShow, showContactOverlay] = useState(false);
  function handleContactOverlay() {
    showContactOverlay(!contactShow);
  }

  function handleContactSubmit(event) {
    event.preventDefault();
    handleContactOverlay();
    let target = event.target;
    console.log("target", event.target);
    const newEmail = {
      name: target.fname.value,
      email: target.femail.value,
      subject: target.fsubject.value,
      content: target.fmessage.value,
    };
    axios.post(`${backend}/resume-site/sendEmail`, {
      name: target.fname.value,
      email: target.femail.value,
      subject: target.fsubject.value,
      content: target.fmessage.value,
    });
    console.log(newEmail);
  }

  return (
    <div className="container">
      {!contactShow && <Header />}
      {!contactShow && <Main onContact={handleContactOverlay} />}
      {contactShow && <ContactOverlay onFormSubmit={handleContactSubmit} />}
    </div>
  );
}

export default App;
